@import '../../assets/scss/com.scss';
.index{
    width: 100%;
    height: 100%;
    background-color: $pageBackColor;
    .index_top{
        width: 100%;
        height: 88px;
        background-color: $myBackColor;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding: 0 31px;
        box-sizing: border-box;
        .text{
            // width: 100px;
            height: 36px;
            font-size: 36px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: #FFFFFF;
            line-height: 35px;
            display: flex;
            align-items: center;
            white-space:nowrap;
            span{
                display: inline-block;
                padding-right: 8px;
                box-sizing: border-box;
            }
            img{
                width: 18px;
                height: 12px;
            }
        }
    }
    .index_main{
        width: 100%;
        // height: 1818px;
        background: #F8F8F8;
        padding: 30px 30px;
        box-sizing: border-box;
    }
}