@import '../../assets/scss/com.scss';
.real{
    margin-top: 25px;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 0px 38px 0px rgba(0, 0, 0, 0.04);
    border-radius: 20px;
    .real_main{
        width: 100%;
        margin-top: 5px;
        .main_box{
            width: 100%;
            height: 180px;
            display: flex;
            justify-content: space-between;
            padding: 20px 30px;
            box-sizing: border-box;
            border-bottom: 3px solid  #EFEFEF;
            .box_right{
                display: flex;
                align-items: center;
            }
            .box_left{
                font-size: 30px;
                font-family: Microsoft YaHei;
                font-weight: 400;
                color: #7E7E7E;
                span{
                    display: inline-block;
                }
                .left_bottom{
                    margin-top: 20px;
                }
                .left_span{
                    text-indent: 30px;
                    font-size: 30px;
                    font-family: Microsoft YaHei;
                    font-weight: bold;
                    color: #363636;
                }
            }
        }
    }
    .real_img{
        width: 100%;
        height: 20px;
    }
    .real_top{
        width: 100%;
        height: 80px;
        display: flex;
        align-items: center;
        padding: 0px 30px;
        box-sizing: border-box;
        box-shadow:0px 10px 10px rgba(0,0,0,0.1);
        .top_left{
            width: 10px;
            height: 10px;
            background: $myBackColor;
            border-radius: 50%;
        }
        .top_right{
            padding-left: 16px;
            box-sizing: border-box;
            font-size: 36px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: #363636;
        }
    }
    .real_fl{
        width: 100%;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            width: 30px;
            height: 26px;
        }
    }
}

.remom_null{
    width: 100%;
    height: 168px;
    padding: 27px 30px;
    background: #FFFFFF;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
    box-shadow: 0px 0px 38px 0px rgba(0, 0, 0, 0.04);
    border-radius: 20px;
    .null_cen{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        img{
            width: 33px;
            width: 33px;
        }
        div{
            height: 24px;
            text-align: center;
            font-size: 24px;
            margin-top: 16px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: #BBBBBB;
        }
    }
}