$myBackColor: #0090FF;//主题颜色
$pageBackColor:#F8F8F8;//页面背景色
.cen{
    //上下居中
    display: flex;
    align-items: center;
}
.navigator_arrow:after {//三角形
    content: " ";
    display: inline-block;
    height: 18px;
    width: 18px;
    border-width: 2px 2px 0 0;
    border-color: #888888;
    border-style: solid;
    transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    position: absolute;
    top: 50%;
    margin-top: -8px;
    right: 14px;
  }
  .headeer:before {//三角形
    content: " ";
    display: inline-block;
    height: 18px;
    width: 18px;
    border-width: 0 0 4px 4px;
    border-color: #fff;
    border-style: solid;
    transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    position: absolute;
    top: 50%;
    margin-top: -8px;
    left: 30px;
  }

  .fadeout {
    -webkit-transition: all 1.5s;
    -moz-transition: all 1.5s;
    -ms-transition: all 1.5s;
    -o-transition: all 1.5s;
    transition: all 1.5s;
    opacity: 0;
}

.fadein {
    -webkit-transition: all 1.5s;
    -moz-transition: all 1.5s;
    -ms-transition: all 1.5s;
    -o-transition: all 1.5s;
    transition: all 1.5s;
    opacity: 1;
}

.slidup {
    animation: slidup 0.4s ease-in both;
  }
  
  @keyframes slidup {
    from {
      transform: translateY(100%);
    }
  
    to {
      transform: translateY(0%);
    }
  }