.index_remom{
    width: 100%;
    padding: 27px 30px;
    background: #FFFFFF;
    box-sizing: border-box;
    .remom_top{
        width: 100%;
        font-size: 36px;
        font-family: Microsoft YaHei;
        font-weight: bold;
        color: #363636;
        position: relative;
    }
    .remom_text{
        width: 100%;
        padding-top: 25px;
        box-sizing: border-box;
        display: flex;
        .text_left{
            width: 50px;
            font-size: 30px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: #7E7E7E;
            white-space:nowrap;
            margin-right: 18px;
        }
        .text_right{
            flex: 1;
            font-size: 30px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: #363636;
            word-wrap:break-word;
            word-break:normal; 
        }
    }
}
.remom_null{
    width: 100%;
    height: 168px;
    padding: 27px 30px;
    background: #FFFFFF;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
    box-shadow: 0px 0px 38px 0px rgba(0, 0, 0, 0.04);
    border-radius: 20px;
    .null_cen{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        img{
            width: 33px;
            width: 33px;
        }
        div{
            height: 24px;
            text-align: center;
            font-size: 24px;
            margin-top: 16px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: #BBBBBB;
        }
    }
}