@import '../../assets/scss/com.scss';
.select{
    display: flex;
    justify-content: space-between;
    font-size: 30px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #7E7E7E;
    .select_list{
        display: flex;
        align-items: center;
        .list_child{
            padding-right: 28px;
            box-sizing: border-box;
        }
        .changeColor{
            font-size: 36px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: #363636;
            padding-right: 28px;
            box-sizing: border-box;
            position: relative;
        }
        .changeColor::after{
            content: '';
            position: absolute;
            bottom: -10px;
            left: 8%;
            width: 53px;
            height: 4px;
            background: $myBackColor;
            border-radius: 2px;
        }
        .changeColorbig{
            font-size: 36px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: #363636;
            padding-right: 28px;
            box-sizing: border-box;
            position: relative;
        }
        .changeColorbig::after{
            content: '';
            position: absolute;
            bottom: -10px;
            left: 26%;
            width: 53px;
            height: 4px;
            background: $myBackColor;
            border-radius: 2px;
        }
    }
    .select_right{
        display: flex;
        align-items: center;
        img{
            width: 34px;
            height: 30px;
        }
        span{
            display: inline-block;
            padding-left: 9px;
            box-sizing: border-box;
        }
    }
}